@import url(https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap);
:root {
  --s-trans-dur: 0;
  --s-trans-animation: ease-out;
  --s-transform-x: -33.3333333%;
  --s-amount: 1; }

.staged-outer {
  position: relative;
  overflow-x: hidden; }
  .staged-outer .staged-left-nav, .staged-outer .staged-right-nav {
    cursor: pointer;
    position: absolute;
    height: 3.75em;
    width: 3.25em;
    top: calc(50% - 1.875em);
    z-index: 1; }
    .staged-outer .staged-left-nav:before, .staged-outer .staged-right-nav:before {
      content: '';
      display: block;
      opacity: 0.2;
      background-color: #fff;
      width: 100%;
      height: 100%; }
    .staged-outer .staged-left-nav:after, .staged-outer .staged-right-nav:after {
      border-top: 3px solid #000;
      border-radius: 3px;
      content: '';
      display: block;
      height: 1.5em;
      width: 1.5em;
      top: calc(50% - 0.75em - 1.5px);
      position: absolute; }
  .staged-outer .staged-left-nav {
    left: 0; }
    .staged-outer .staged-left-nav:before {
      border-radius: 0 2.25em 2.25em 0; }
    .staged-outer .staged-left-nav:after {
      left: 1em;
      border-left: 3px solid #000;
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg); }
  .staged-outer .staged-right-nav {
    right: 0; }
    .staged-outer .staged-right-nav:before {
      border-radius: 2.25em 0 0 2.25em; }
    .staged-outer .staged-right-nav:after {
      right: 1em;
      border-right: 3px solid #000;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg); }

.staged {
  width: 300%;
  -webkit-transform: translateX(-33.3333333%);
          transform: translateX(-33.3333333%);
  -webkit-transform: translateX(var(--s-transform-x));
          transform: translateX(var(--s-transform-x));
  -webkit-transition: -webkit-transform 0 ease-out;
  transition: -webkit-transform 0 ease-out;
  transition: transform 0 ease-out;
  transition: transform 0 ease-out, -webkit-transform 0 ease-out;
  -webkit-transition: -webkit-transform var(--s-trans-dur) var(--s-trans-animation);
  transition: -webkit-transform var(--s-trans-dur) var(--s-trans-animation);
  transition: transform var(--s-trans-dur) var(--s-trans-animation);
  transition: transform var(--s-trans-dur) var(--s-trans-animation), -webkit-transform var(--s-trans-dur) var(--s-trans-animation); }
  .staged .staged-child {
    vertical-align: top;
    width: calc(33.333333% / 1);
    width: calc(33.333333% / var(--s-amount));
    display: inline-block; }

