img.clicked {
  -webkit-animation: opacity-click 250ms linear;
          animation: opacity-click 250ms linear; }

@-webkit-keyframes opacity-click {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes opacity-click {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

